// 
<template>
    <div class="home">
        <Seacrh></Seacrh>
        <Navigation @toggleNavClick='toggleNavClick'></Navigation>
        <div class="list">
            <base-list :data='listData' v-if="listData.length > 0" @nextPage='nextPage'></base-list>
            <div class="empty-page" v-else>
                <img src="../../assets/list-empty.png" class="empty-pic">
                <span class="empty-text">{{$t('empty.listEmpty')}}</span>
            </div>
        </div>
        <Go-home></Go-home>
    </div>
</template>

<script>
import Seacrh from '../../components/common/search';
import Navigation from '../../components/home/navigation';
import BaseList from '../../components/common/list';
import GoHome from '../../components/common/go-home';
import { mapState } from 'vuex';
import { publicPost } from '../../js/api/index.js';
import getApiUrl from '../../js/api/apiUrlList';
export default {
    data () {
        return {
            navId: '', // id
            listData: [], // 
            postData: {
                pageNo: 1,
                pageSize: 10
            }
        };
    },
    computed: {
        ...mapState({
            tradeType: state => state.home.tradeType,
            serveType: state => state.home.serveType
        })
    },
    methods: {
        // 
        getListData () {
            if (this.tradeType) {
                this.postData.industryType = this.tradeType.type;
            }
            this.postData.requestType = this.serveType && this.serveType.type ? this.serveType.type : 1;
            publicPost(getApiUrl('getHomeList'), this.postData).then((res) => {
                if (this.postData.pageNo === 1) {
                    this.listData = res;
                } else {
                    this.listData = this.listData.concat(res);
                }
            }).catch(() => {
                console.warn('error');
            });
        },
        // 
        toggleNavClick () {
            this.postData = {
                pageNo: 1,
                pageSize: 10
            };
        },
        // 
        nextPage (page) {
            this.postData.pageNo = page;
            this.getListData();
        }
    },
    components: {
        Seacrh,
        Navigation,
        BaseList,
        GoHome
    },
    created () {
        this.$router.setTitle(this.$t('title.home'));
        this.getListData();
    },
    watch: {
        serveType () {
            this.getListData();
        }
    }
};
</script>
<style>
    .home {
        background: #fff;
        height:100%;
    }
</style>
