<template>
    <div class="navigation">
        <div class="navigation-bar"
            ref='navigationBar'>
          <ul class="navigation-content"
              ref="content">
            <!-- :to="item.link"-->
            <li v-for="(item, index) in navList"
                class="navigation-list navigation-list-item"
                :class="{'has-sub':activeInd == index}"
                :key="index"
                @click="toggleNavClick(index,$event,item)">
                {{item.name}}
            </li>
          </ul>
        </div>
        <div class="navigation-screen"
            @click="allNav">
            <img v-if="tradeType && tradeType.type" src="../../assets/screen-active.png">
            <img v-else src="../../assets/screen-default.png" alt="">
        </div>
    </div>
</template>

<script>

import BScroll from 'better-scroll';
import { mapState, mapMutations, mapActions } from 'vuex';
export default {
    data () {
        return {
            activeInd: 0
        };
    },
    computed: {
        ...mapState({
            serveType: state => state.home.serveType,
            navList: state => state.home.navList,
            tradeType: state => state.home.tradeType
        })
    },
    methods: {
        ...mapActions({
            getTypeList: 'getTypeList'
        }),
        ...mapMutations({
            setServeType: 'setServeType',
            setNavList: 'setNavList'
        }),
        // 
        initScroll () {
            this.scroll = new BScroll(this.$refs.navigationBar, {
                probeType: 3,
                click: true,
                tap: true,
                scrollX: true
            });
        },
        // 
        toggleNavClick (index, e, item) {
            this.$nextTick(() => {
                this.scroll.scrollToElement(e.target, 200, true, true, 'easing');
            });
            // 
            this.setServeType({
                ind: index,
                type: item.code
            });
            this.activeInd = index;
            this.$emit('toggleNavClick');
        },
        // 
        allNav () {
            this.$router.push({
                path: '/trade-type'
            });
            this.setNavList([]);
        }
    },
    created () {
        let params = {
            type: 2
        };
        this.getTypeList(params);
    },
    mounted () {
        this.initScroll();
        // TODO: serveType,,
        if (this.serveType) {
            this.activeInd = this.serveType.ind;
            let activeDom = document.querySelector('.navigation-content .has-sub');
            this.scroll.scrollToElement(activeDom, 1000, true);
        }
    }
};
</script>

